<template>
  <div class="container center">
    <!-- @submit.prevent="userLogin()" <script type="text/x-template" id="login-form"> -->

      <form id="Login" class="boxx col col-lg-6" name="userLogin" method="post">

        <h2>{{ Header }}</h2>
        <div class="form-group">
          <label>Username</label>
          <input type="text" class="form-control" id="username" v-model="user.username" name="username" placeholder="Username">
        </div>

        <div class="form-group">
          <label>Password</label>
          <input type="password" class="form-control" v-model="user.password" name="password" id="password" placeholder="Password">
        </div>

        <input v-on:click.prevent="userLogin" class="btn btn-success" type="submit" value="Sign In">
        <!-- <b-button v-on:click.prevent="userLogin" variant="success">Sign in</b-button> -->
        <b-button type="button" href="/" variant="outline-primary">Back</b-button>

      </form>

    <!-- </script> -->
  </div>
</template>

<script>
import axios from 'axios'
import vue from 'Vue'
  
export default {
  name: 'Login',

  data() {
    return {
      Header: 'Login',
      user: {
        username: "",
        password: ""
      },
    }
  },

  methods: {
    userLogin() {
      axios.post('http://edubox.pinisi.io/tarqi/api/post/?model=login&type=android', this.user)
      
      .then(function (response) { 
        alert(response); 

        localStorage.setItem('username', this.user.username);

      })
      
      .catch(function (error){
        alert(error);

      })
      console.log('name : ' + this.user.username, 'Pass : ' + this.user.password)
      // localStorage.setItem('username', this.user.username)
      // localStorage.setItem('password', this.user.password)
      // return login

    },
  }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.boxx{
  padding: 20px;
  background-color: rgb(199, 199, 199);
  border-radius: 10px;
}
</style>


