<template>
  <div class="container">
    <h1>{{ Header }}</h1>
    <table striped hover class="table">
      <thead>
        <th>No. </th>
        <th>Nama Guru</th>
        <th>Jumlah Kehadiran</th>
      </thead>
      <tr v-for="data in guru.data.results">
        <td>{{ data.id }}</td>
        <td><router-link to="Detail">{{ data.display_name }}</router-link></td>
        <td>0</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Hello',
  data () {
    return {
      Header: 'Rekap Kehadiran Guru',
      guru: {},
    }
  },
  mounted(){
    this.getGuru();
  },
  methods: {
    async getGuru(){
      var guru = await axios.get('http://edubox.pinisi.io/tarqi/api/list/user', {
        params: {
          model: 'user',
          token: '4I7s9OjWPFOjzM2RBKxFuldEobjzBnUO0IXBu2Hcym0ztJABvz',
          type: 'teachers'
        }
      });
      this.guru = guru.data
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


