import Vue from 'vue'
import Router from 'vue-router'
import rekapIndex from '@/components/rekapIndex'
import Detail from '@/components/Detail'
import Login from '@/components/Login'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/rekapIndex',
      name: 'rekapIndex',
      component: rekapIndex
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    }
  ],
  mode: 'history'
})



